import "./Courses.css";
import coursesBase from "./CoursesBase";

const CoursesCard = ({ onClickOpenModal }) => {
  return (
    <>
      {coursesBase.map((item) => (
        <div
          className="col-lg-4 col-md-6 col-12 mb-md-5 mb-4 courses-card"
          data-aos={item.aos}
          data-aos-duration="800"
          data-aos-delay={item.aosDelay}
          data-aos-once="true"
          key={item.id}
        >
          <div className="img-box">
            <img src={item.imgUrl} alt={item.title} />
          </div>
          <div className="text-box">
            <h3>{item.title}</h3>
            <p className="card-text">{item.info}</p>
            <button onClick={() => onClickOpenModal()}>Kursga yozilish</button>
          </div>
        </div>
      ))}
    </>
  );
};

export default CoursesCard;
