import "./Courses.css";
import CoursesCard from "./CoursesCard";

const Courses = ({ onClickOpenModal, activeCourses }) => {
  return (
    <section
      className={activeCourses ? "courses courses-click-style" : "courses"}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Kurslar
      </h2>
      <div className="row align-items-start">
        <CoursesCard onClickOpenModal={onClickOpenModal} />
      </div>
    </section>
  );
};

export default Courses;
